/**
 * Matador Jobs Admin CSS
 * Author:
 */

.column-matador-sync-status,
.column-matador-actions {
  width: 15%;
}

.matador-logging-sync-state {
  float: right;
}

/* Matador Settings */

.matador-settings-tab {
  overflow: auto;
}

.matador-settings-section {
  padding: 2rem;
  background: white;
  overflow: auto;
  font-size: 1rem;
  line-height: 1.5rem;

  + .matador-settings-section {
    margin-top: 1rem;
  }

  h4 {
    margin: 0 0 1.5rem;
    font-size: 1.5rem;
    line-height: 2rem;
  }

  p {
    font-size: 1rem;
  }

  .checkboxes {
    li {
      list-style: none;
      position: relative;
    }
    li + li {
      margin-top: .5rem;
    }
  }
  .checklist{
    li {
      list-style: none;
      padding-left: 1.25rem;
      position: relative;
    }
    li:before {
      position: absolute;
      left: 0;
      content: '\2713\0020';
    }
  }

  label {
    display: block;
  }
  label + label,
  label + button {
    margin-top: 1rem;
  }
  footer {
    margin-top: 2rem;
    border-top: 1px solid #23282d;
    padding-top: 2rem;
    text-align: center;
  }
  footer p {
    font-style: italic;
    margin-bottom: 2rem;
    margin-top: 0;
    text-align: left;
  }
  footer button:first-of-type {
    float: left;
  }
  footer button:last-of-type {
    float: right;
  }
  footer button[name=exit] {
    float: none;
  }
  footer button[name=exit]:last-of-type {
    float: right;
  }
  code, kbd, pre {
    margin: 1rem 0;
    padding: .5rem 2rem;
    background: rgba(0, 0, 0, .07);
  }

  pre {
    white-space: pre-wrap;
    padding: 3rem 2rem;
  }

  .matador-field-group {
    max-width: 800px;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: flex-start;
    + .matador-field-group {
      margin-top: 2rem;
    }
  }

  .matador-label {
    flex: 0 1 28%;
  }

  .matador-field-label {
    font-size: 1rem;
    font-weight: normal;
    margin: 0 0 1rem;
  }

  .matador-field-sublabel {
    font-size: 0.875rem;;
    margin: 0 0 0.875rem;;
    font-weight: normal;
  }

  .matador-field-description {
    font-size: 0.875rem;
    margin: 0 0 1rem;
  }

  * + .matador-field-description {
    margin-top: 1rem;
  }

  .matador-field {
    flex: 0 1 68%;
  }

  .callout {
    background: #fafafa;
    border-left: .25rem solid #fff;
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .1);
    margin: 1em 0;
    padding: .25rem .75rem;

    p {
      position: relative;
      padding-left: 2rem;
      margin: 0;
      line-height: 1.875rem;
      &:before {
        display: inline-block;
        font-family: 'dashicons';
        font-size: 1.75rem;
        line-height: 1.75rem;
        position: absolute;
        top: .375rem;
        left: 0;
      }
    }

    &.callout-error {
      border-left-color: #ca4a1f;
      p:before {
        color: #ca4a1f;
        content: "\f158";
      }
    }

    &.callout-warning {
      border-left-color: #ffb900;
      p:before {
        color: #ffb900;
        content: "\f534";
      }
    }

    &.callout-success {
      border-left-color: #46b450;
      p:before {
        color: #46b450;
        content: "\f147";
      }
    }

    &.callout-info {
      border-left-color: #00a0d2;
      p:before {
        color: #00a0d2;
        content: "\f348";
      }
    }
  }
}

.active-license {
  margin-left: 1.25rem;
  line-height: 2rem;
}

.matador-meta-scroll-box {
  height: 12.5rem;
  overflow-y: scroll;
}

.matador-settings-form {

  input[type=text],
  input[type=search],
  input[type=tel],
  input[type=time],
  input[type=url],
  input[type=week],
  input[type=password],
  input[type=color],
  input[type=date],
  input[type=datetime],
  input[type=datetime-local],
  input[type=email],
  input[type=month],
  input[type=number],
  select,
  textarea {
    width: 80%;
    padding: .75rem;
  }

  select {
    height: auto;
  }

  .matador-field-type-toggle {
    .matador-switch {
      position: relative;
      height: 2rem;
      margin: 0 0 2rem;
      font-size: 0;
    }
    label {
      font-size: 1.25rem;
      line-height: 2.25rem;
      display: inline-block;
      width: 3rem;
      height: 100%;
      margin: 0;
      text-align: right;
      opacity: 0.66;
      transition: opacity 0.25s ease;
      cursor: pointer;
      box-sizing: border-box;
      padding-right: 1rem;
      &:last-of-type {
        margin-left: 4rem;
        width: 4rem;
        text-align: left;
        padding-left: 1rem;
        padding-right: 0;
      }
    }
    input {
      height: 2.25rem;
      width: 4.25rem;
      margin: 0;
      position: absolute;
      top: 0;
      left: 3rem;
      cursor: pointer;
      opacity: 0;
      z-index: 2;
      &:checked{
        z-index: 1;
      }
      &:checked + label {
        opacity: 1;
        cursor: default;
      }
      &:disabled + label {
        opacity: .5;
      }
      &:disabled,
      &:disabled + label {
        cursor: not-allowed;
      }
    }
    .switch-outside {
      background: #ddd;
      position: absolute;
      width: 3.5rem;
      left: 3rem;
      height: 1.5rem;
      padding: .25rem;
      overflow: hidden;
      border: 1px solid #ccc;
      border-radius: 2px;
      transition: 0.25s ease all;
    }
    .switch-inside {
      position: absolute;
      transition: 0.25s ease all;
      box-sizing: border-box;
      border: 1px solid #ccc;
      border-radius: 2px;
      height: 1.5rem;
      width: 1.5rem;
    }
    input ~ input:checked ~ .switch-outside .switch-inside {
      left: 2.25rem;
      background: #999;
    }
    input:checked ~ input ~ .switch-outside .switch-inside {
      left: 0.25rem;
      background: #0085ba;
      border-color: #0073aa #006799 #006799;
    }
    input:checked:disabled ~ input:disabled ~ .switch-outside .switch-inside {
      background: #999;
      border-color: #ccc;
    }
  }

  .radio-options,
  .checkbox-options {
    list-style-type: none;
    margin-bottom: 1rem;
    margin-left: 0;
  }
  .radio-options li {
    display: inline-block;
    margin-right: 1rem;
    &:before {
      display: none;
    }
  }

  #general-options-form-submit {
    margin-top: 2rem;
  }
}

.bullhorn-api-assistant {

  max-width: 50rem;
  border: 1px solid white;
  position: relative;

  .bullhorn_expert_mode {
    position: absolute;
    top: -30px;
    right: 6px;
  }

  .progress {
    margin: 0;
    list-style: none;
    overflow: auto;
    display: flex;
    flex-flow: row nowrap;
    li {
      flex: 0 1 25%;
      text-align: center;
      padding: 5rem 0 2rem;
      margin-bottom: 0;
      position: relative;
      background: #46b450;
      color: #fff;

      + li {
        border-left: 1px solid white;
      }

      &:before {
        display: block;
        content: "\f147";
        font-family: "dashicons";
        font-size: 1.5rem;
        color: #46b450;
        position: absolute;
        top: 2rem;
        left: 50%;
        margin-left: -1.25rem;
        border-radius: 50%;
        background: white;
        height: 2.5rem;
        width: 2.5rem;
        line-height: 2.5rem;
        z-index: 2;
      }
      &:after {
        content: ' ';
        position: absolute;
        display: block;
        border-top: .25rem solid white;
        width: 100%;
        top: 3.175rem;
        z-index: 1;
      }

      &:first-child:after,
      &:last-child:after {
        width: 50%;
      }
      &:first-child:after {
        left: 50%;
      }

      &.active {
        background: #0073aa;
        &:before {
          color: #0073aa;
        }
      }

      &.active,
      &.active ~ li {
        &.prepare:before {
          content: "\f481";
        }
        &.credentials:before {
          content: "\f112";
        }
        &.callback:before {
          content: "\f525";
        }
        &.authorize:before {
          content: "\f528";
        }
        &.datacenter:before {
          content: "\f176";
        }
      }

      &.active ~ li {
        background: transparent;
        color: #0073aa;
        &:before {
          color: #0073aa;
        }
        a {
          color: #0073aa;
        }
      }
    }
  }

  .clusters-table {
    border: 1px solid #23282d;
    border-collapse: collapse;
    padding: .5rem;
    margin: 2rem 0;
    width: 100%;

    th,
    td {
      border: 1px solid #23282d;
      border-collapse: collapse;
      padding: .5rem;
    }

    th {
      text-align: left;
    }

  }

}

//job manger support

.job_listing_page_job-manager-settings #settings-matador {
  .radio-group, .checkbox-group {
    label {
      padding-right: 1em;
    }
  }
  input[type=text],
  input[type=search],
  input[type=tel],
  input[type=url],
  input[type=password],
  input[type=email],
  select,
  textarea{
    width: 25em;
  }
  textarea{
    height: 12em;
  }
}

.widefat .column-job_application_actions{
  a.icon-sync{
    &:before{
      content: '\00a0';
      background-image: url("https://app.bullhornstaffing.com/assets/images/circle-bull.png");
      background-repeat: no-repeat;
      background-size: 16px;
      background-position: 3px 4px;
    }
  }
}